<script>
export default {
  components: {},
  data() {
    return {
      title: ["学科建设", "授课动态"],
      info: [],
      course: []
    }
  },
  methods: {
    async getDepartmentNews() {
      let Resources = await this.$api.getDepartmentNews()
      this.info = Resources.data.info
      this.course = Resources.data.course
    },
    Wow() {
      let wow = new this.$Wow.WOW({
        boxClass: 'wow',
        animateClass: 'animate__animated',
        offset: 0,
        mobile: true,
        live: true
      });
      wow.init();
    }
  },
  created() {
    this.getDepartmentNews();
  },
  mounted() {
    this.Wow()
  },
}
</script>

<template>
  <div class="department-news">
    <div class="content">
      <div class="title">
        <div class="title-content wow animate__animated animate__zoomIn ">
          <i class="tethering-icon"></i>
          <span>{{ title[0] }}</span>
        </div>
      </div>
      <div class="discipline wow animate__animated animate__fadeInRight">
        <div v-for="value in info">
          <div>
            <div class="hvr-grow">
              <img :src="value.imgUrl"
                   alt="" class="hvr-grow">
            </div>
            <div>{{ value.rate }}</div>
          </div>
          <div>
            <div>{{ value.name }}</div>
            <div>{{ value.manage }}</div>
          </div>
        </div>
      </div>
      <div class="title wow animate__animated animate__zoomIn">
        <div class="title-content">
          <i class="teaching-icon"></i>
          <span>{{ title[1] }}</span>
        </div>
      </div>
      <div class="teaching-dynamics wow animate__animated animate__zoomIn">
        <div v-for="value in course">
          <a :href="value.link" target="_blank">
            <div class="teaching-title">{{ value.data }}</div>
            <i class="segmentation"></i>
            <span class="teaching-content">{{ value.courseTitle }}</span>
            <span class="teaching-button">
              <i class="teacher-icon"></i>讲师：{{ value.lecturer }}
            </span>
          </a>
          <div class="college">{{ value.status }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<style scoped lang="stylus">
.tethering-icon
