  height 25px
  background url("https://static-cshu.cloudmiao.cn/static/sheke/images/Chinese/department-news/icon/tethering.png") no-repeat
  background-size 25px

.teaching-icon
  display block
  width 25px
  height 25px
  background url("https://static-cshu.cloudmiao.cn/static/sheke/images/Chinese/department-news/icon/chat.png")
  background-size 25px

.teacher-icon
  display block
  width 20px
  height 20px
  background url("https://static-cshu.cloudmiao.cn/static/sheke/images/Chinese/department-news/icon/user.png")
  background-size 20px

.department-news
  width 100%
  background url("https://static-cshu.cloudmiao.cn/static/sheke/images/Chinese/department-news/department-news_bg.png") no-repeat center

.content
  margin 0 auto
  width 1200px
  height 100%

.title
  padding 30px 0
  align-items center
  width 100%
  height 31px

  > .title-content
    display flex
    align-items center

    > span
      margin-left 5px
      font-size 24px
      font-weight bold
      color #FFFFFF

.discipline
  display flex
  justify-content space-between
  width 100%
  height 300px

  > div
    box-sizing border-box
    display flex
    flex-direction column
    width 380px
    height 100%
    background #FFFFFF

    > div:first-of-type
      overflow: hidden;
      position relative
      width 100%
      height 215px

      > div:first-of-type
        position relative
        width 100%
        height 100%

        > img
          position absolute
          top 50%
          transform translateY(-50%)
          width 100%

      > div:last-of-type
        display flex
        align-items center
        justify-content center
        position absolute
        left 0
        bottom 0
        width 110px
        height 35px
        background #725BA1
        color #FFFFFF

    > div:last-of-type
      flex 1
      display flex
      flex-direction: column;
      justify-content center
      text-align center

      > div:first-of-type
        margin-bottom 10px
        font-size 18px
        font-weight bold
        color #725BA1

.teaching-dynamics
  padding-bottom 30px
  display flex
  align-items center
  justify-content space-between

  > div
    position relative
    display flex
    align-items center
    justify-content center
    width 290px
    height 180px
    background #FFFFFF

    > a
      display flex
      flex-direction: column;

      > div, span
        width 220px

      > .teaching-title
        font-size 18px
        color #725ba1

      > .segmentation
        display block
        width 50px
