        margin 10px 0

      > .teaching-content
        overflow hidden
        white-space nowrap
        text-overflow ellipsis

      > .teaching-button
        display flex
        align-items center
        margin-top 25px
        font-size 14px

        > i
          margin-right 3px

.college
  display flex
  align-items center
  justify-content center
  position absolute
  right 5px
  top 5px
  width 100px
  height 30px
  background #725ba1
  font-size 14px
  color #FFFFFF
</style>